.wellness__homeServicesRight {
    display: flex;
    justify-content: space-evenly;
    padding: 2rem 5rem;
    height: 100%;
    background-color: var(--color-secondary);
}

.wellness__homeServicesRight-noView {
    opacity: 0;
}

.wellness__homeServicesRight-animation {
    animation-name: slidein;
    animation-duration: 1.5s;
}

.wellness__homeServicesRight-text {
    width: 50rem;
    margin: auto 2rem;
}

.wellness__homeServicesRight-text h1 {
    color: var(--color-text);
}

.wellness__homeServicesRight-img {
    width: 50rem;
}

.wellness__homeServicesRight-img img {
    border-radius: 2rem;
    max-width: 100%;
    height: auto;
    max-height: 30rem;
    margin: auto;
}

@keyframes slidein {
    from {
        transform: translate3d(0,5rem,0);
        opacity: 0;
    }
    20% {
        transform: translate3d(0,5rem,0);
        opacity: 0;
    }
    to {
        transform: translate3d(0,0,0);
    }
}